[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;

  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;



  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-gplus:before { content: '\e800'; } /* '' */
.icon-location:before { content: '\e801'; } /* '' */
.icon-phone:before { content: '\e802'; } /* '' */
.icon-mail:before { content: '\e803'; } /* '' */
.icon-twitter:before { content: '\f099'; } /* '' */
.icon-facebook:before { content: '\f09a'; } /* '' */
.icon-linkedin:before { content: '\f0e1'; } /* '' */
.icon-dribbble:before { content: '\f17d'; } /* '' */
.icon-pinterest:before { content: '\f231'; } /* '' */

h1, h2, h3, h4, p {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth; /* свойство scroll-behavior не наследуется, применяется к прокручиваемым блокам */
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
}

code {
  background: rgba(#000, 0.2);
  padding: 5px;
  margin-right: 5px;
}

a:active, /* активная/посещенная ссылка */
a:hover, /* при наведении */
a {
  text-decoration: none;
}
section img{
  width: 100%;
}

body {
  font-family: 'Maven Pro', sans-serif;

}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #bebebe;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #bebebe;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #bebebe;
}
:-moz-placeholder { /* Firefox 18- */
  color: #bebebe;
}
.form input::placeholder,
.get-in-touch-form textarea::placeholder {
  letter-spacing: -0.4px;
}

.form input::-ms-input-placeholder,
.form textarea::-ms-input-placeholder {
  letter-spacing: -0.4px;
}

.form input:-ms-input-placeholder,
.form textarea:-ms-input-placeholder {
  letter-spacing: -0.4px;
}


.container {
  max-width: 820px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 20px;
  box-sizing: border-box;
}

header {
  height: 63px;
  background: #323232;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 26px 45px 0 45px;
    border-color: #323232 transparent transparent transparent;
    line-height: 0px;
    _border-color: #323232 #000000 #000000 #000000;
    _filter: progid:DXImageTransform.Microsoft.Chroma(color='#000000');
    bottom: -25px;
    left: calc(50% - 45px);
    z-index: 9;
  }
}


.menu {
  font-family: 'Lato', sans-serif;
  display: flex;
  justify-content: center;

  &__list {
    display: flex;
    padding-top: 31px;
    position: relative;
    &:before, &:after {
      content: "";
      width: 2px;
      height: 2px;
      position: absolute;
      top: 41%;
      left: 19%;
      background-color: #474c5f;
    }
    &:after{
      right: 21%;
      left: unset;
    }
    &__item {
      list-style: none;
      &:not(:last-child) {
        margin-right: 25px;
      }
      &:nth-child(-n+2){
        position: relative;
        right: -5px;
      }

      a {
        text-transform: uppercase;
        font-size: 1rem;
        font-weight: 700;
        color: #ffffff;
        letter-spacing: -0.1px;
        &:hover{
          color: #2eb398;
        }

      }

    }
  }
}

.logo {
  position: relative;
  top: -21px;
  padding: 0 15px;
}

.hexagon {
  width: 180px;
  height: 207px;

  margin: 10px;
  transform: rotate(120deg);
  overflow: hidden;
}

.hex-layer-1 {
  width: 100%;
  height: 100%;

  transform: rotate(-60deg);
  overflow: hidden;
}
.hex-layer-2 {
  width: 100%;
  height: 100%;

  transform: rotate(-60deg);
  overflow: hidden;

}
.hex-row{
  display: flex;
  justify-content: center;
}
.hex-row:not(:first-child){
  margin-top: -59px;
}
.even .hexagon{
  position: relative;

}
#hexagon{
  position: relative;
  padding: 80px 0 78px 0;
}
#about {
  background: #2eb398;
  padding: 69px 0 65px 0;
}
#contacts{
  position: relative;
  background: #323232;
  padding: 60px 0 64px 0;

}
.about{
  display: flex;
  .hexagon{
    min-width: 180px;
    margin: 0;
  }
  &__content{
    padding-left: 20px;
        &__title{
          font-size: 3rem;
          color: #d7fcf5;
          letter-spacing: -0.6px;
          word-spacing: 2px;
          line-height: 0.6;
        }
    &__subtitle{
      font-size: 1.8rem;
      color: #20816d;
      padding: 8px 0 14px;
      word-spacing: -1px;
    }
    &__text{
      font-size: 1.2rem;
      color: #d7fcf5;
      line-height: 1.797;
      word-spacing: -1px;
      letter-spacing: -0.3px;
      text-align: justify;
    }
  }
}

.social{
  display: flex;
  justify-content: flex-end;
  margin-top: 37px;
  li {
    list-style: none;
  }
    &__box {
      width: 24.5px;
      height: 24.5px;
      background: #a4dbd4;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.4rem;
      color: #2eb398;


  }
}
.contacts{
  color: #e0e0e0;
  &__title{
    font-size: 3rem;
  }
  &__subtitle{
    font-size: 1.8rem;
  }
  &__info{
    p{
      font-family: "Lato";
      font-weight: 400;
      color: rgb(224, 224, 224);
      line-height: 2.1;
      text-align: justify;
      word-wrap: break-word;
      letter-spacing: 1px;
      span{
        font-weight: 900;
      }
         }


  }
}
.contacts__box{
  padding-top: 35px;
a{
  text-decoration: none;
  color: #e0e0e0;
}
  p{
    display: flex;
    align-items: center;
    font-size: 12px;
    font-family: "Lato";
    font-weight: 700;
    line-height: 1.55;
    letter-spacing: 0.2px;
    .contacts-icon {
      margin-right: 14px;
      color: #2eb398;
      font-size: 14px;
    }
  }
}
.form{
  flex-basis: 74.5%;

}
.contacts__info{
  flex-basis: 23%;
  margin-top: -5px;
}
.form textarea{
  height: 150px;
}
.form input,
.form textarea {
  outline: 0;
  background: #454545;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 13px;
  box-sizing: border-box;
  font-size: 12px;
}
.form input:hover, .form textarea:hover, .form  input:focus {
  border-color: rgba(150, 150, 150, 0.5);
  box-shadow: inset 0 0 0 1px rgba(150, 150, 150, 0.5);
}
.form  input:focus {
   outline: none;
 }

.pin, .in{
  background: #acddd7;
}
.fb{
  background: #a5dbd4;
}
.tw,.dribbble{
  background: #6ccbbe;
}
.gplus{
  background: #7ed0c5;
}
.fb:hover{
  background-color: #3b5998;
  color: #f8f8f8;
}
.tw:hover{
  background-color: #00a9ed;
  color: #f8f8f8;
}
.gplus:hover{
  background-color: #d64a37;
  color: #f8f8f8;
}
.pin:hover{
  background-color: #bd071d;
  color: #f8f8f8;

}
.in:hover{
  background-color: #015886;
  color: #f8f8f8;
}
.dribbble:hover{
  background-color: #d92d84;
  color: #f8f8f8;
}
.contact_row{
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
}
.btn {
  display: inline-block;
  padding: 0 12px;
  font-weight: 400;
  text-align: center;
  cursor: pointer;
  user-select: none;
  border: 1px solid transparent;

  line-height: 35px;
  width: 90px;
  background: #2eb398;
  font-family: 'Lato';
  font-weight: 700;
  font-size: 12px;
  color: #d7fcf5;
}
.btn:hover, .btn:focus {
  color: #22b382;
  background-color: #d1fafc;
}
.send-btn{
  float: right;
}
footer{
  height: 43px;
  background: #222222;
  display: flex;
  align-items: center;
  padding: 0 40px;
  .copy{
    font-size: 1.2rem;
    color: #454545;
  }
}
.menu__list__item .active{
  color: #2eb398;
}
.visuallyhidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(0px 0px 99.9% 99.9%);
  clip-path: inset(0px 0px 99.9% 99.9%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
}
#hexagon:after, #contacts:after, #contacts:before{
  content: '';
  position: absolute;
  bottom: 11px;
  left: 0;
  right: 0;
  margin: auto;
  width: 960px;
  height: 1px;
  background: #e4e9eb ;
}
#contacts:after, #contacts:before{
  background: #2b2b2b ;
}
#contacts:before{
  top: 11px;
  bottom: unset;
}
.left-line, .right-line, .about, main{
  position: relative;
}
.left-line:before{
  content: '';
  position: absolute;
  left: -10px;
  top:0;
  bottom: 0;
  margin: auto;
  width: 1px;
  background: #e4e9eb ;
  height: 105px;
}
.right-line:before{
  content: '';
  position: absolute;
  right: -10px;
  top:0;
  bottom: 0;
  margin: auto;
  width: 1px;
  background: #e4e9eb ;
  height: 105px;
}
.about:before, .about:after{
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 1px;
  height: 316px;
  background: #219d84 ;
}
.about:before{
  left: -21px;
}
.about:after{
  right: -21px;
}

main:before{
  content: '';
  position: absolute;
  top: 11px;
  left: 0;
  right: 0;
  margin: auto;
  width: 960px;
  height: 1px;
  background: #e4e9eb ;
}
main:after{
  content: '';
  position: absolute;
  top: 11px;
  left: 0;
  right: 0;
  margin: auto;
  width: 140px;
  height: 1px;
  background: #fff ;
}
.slider_track{
  position: absolute;
  bottom: -55px;
  left: 30px;
  display: flex;
  &_item{
    width: 6px;
    height: 6px;
    background: #ddd2c5 ;
    &:not(:last-child){
      margin-right: 3px;
    }
    &.active{
      background: #8b8784;
    }
  }
}
.hex-container{
  position: relative;
  max-width: 1020px;
  margin: 0 auto;

}
.hex-layer-2:hover::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}
@media (max-width: 992px){
  .left-line:before,.right-line:before {
     height: 56px;
  }
  .hex-row:not(:first-child) {
    margin-top: -30px;
  }
  .hexagon {
    width: 101px;
    height: 116px;
    margin: 3px;
  }
  .about .hexagon {
    min-width: 101px;

  }
  #hexagon:after, #contacts:after, #contacts:before,main:before {
    width: 710px;

  }
}
@media (max-width: 768px){
  header:before {
    border-width: 14px 30px 0 30px;
    bottom: -13px;
    left: calc(50% - 30px);
  }
  .contact_row {
        flex-direction: column;
  }
  .about {
    flex-direction: column;
  }
  .hide_logo{
    display: none;
  }
  .contacts__info{
    margin-top: 20px;
  }
  .about .hexagon {
    margin-bottom: 20px;
  }
  .about__content {
   padding-left: 0;
  }
  #about {
    padding: 30px 0;
  }
  footer{
    justify-content: center;
  }
  #hexagon:after, #contacts:after, #contacts:before,main:before {
    width: 485px;

  }

}
@media (max-width: 540px){
  .social__box {
    font-size: 1rem;
    }
  .contacts__subtitle {
    font-size: 1.1rem;
  }
  .contacts__title {
    font-size: 2.4rem;
  }
  .hex-row:not(:first-child) {
    margin-top: -22px;
  }
  .hexagon {
    width: 60px;
    height: 72px;
    margin: 2px;

  }
  .about .hexagon {
    min-width: 60px;

  }
  #hexagon {
    padding: 30px 0;
  }
  #hexagon:after, #contacts:after, #contacts:before,main:before {
    width: 275px;

  }
  .left-line:before, .right-line:before {
    height: 30px;
  }
  .slider_track {
       bottom: -10px;
     }
}

